// src/pages/user/Groups.tsx
import React, { useState } from 'react'
import { 
  MagnifyingGlassIcon,
  AdjustmentsHorizontalIcon,
  UserGroupIcon,
  ChartBarIcon,
  ExclamationTriangleIcon,
} from '@heroicons/react/24/outline'
import { useGetGroupsQuery, GroupType } from '@/store/api/groupsApi'
import { useAuth } from '@/hooks/useAuth'
import GroupListCard from '@/components/common/UI/GroupListCard'
import LoadingSpinner from '@/components/common/UI/LoadingSpinner'

const Groups: React.FC = () => {
  const { user } = useAuth()
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedType, setSelectedType] = useState<GroupType | 'ALL'>('ALL')
  const [showInactive, setShowInactive] = useState(false)
  
  // Fetch groups with current filters
  const {
    data: groupsData,
    isLoading,
    isError,
    error,
  } = useGetGroupsQuery({
    search: searchTerm,
    type: selectedType === 'ALL' ? undefined : selectedType,
    isActive: showInactive ? undefined : true,
    limit: 50, // Show more groups on this page
  })

  const groups = groupsData?.groups || []

  // Filter options
  const typeOptions: { value: GroupType | 'ALL'; label: string }[] = [
    { value: 'ALL', label: 'All Groups' },
    { value: 'CELL', label: 'Cells' },
    { value: 'COMMITTEE', label: 'Committees' },
    { value: 'OFFICE_BEARERS', label: 'Office Bearers' },
    { value: 'ADVISORS', label: 'Advisors' },
  ]

  const handleGroupView = (group: any) => {
    // Navigate to group detail page or show modal
    console.log('View group:', group)
  }

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  if (isError) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <ExclamationTriangleIcon className="w-12 h-12 text-red-500 mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
            Failed to Load Groups
          </h2>
          <p className="text-gray-600 dark:text-gray-400">
            {(error as any)?.data?.message || 'Something went wrong'}
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Page Header */}
      <div className="mb-8">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 dark:text-white flex items-center">
              <UserGroupIcon className="w-8 h-8 mr-3 text-blue-600" />
              Organization Groups
            </h1>
            <p className="text-gray-600 dark:text-gray-400 mt-2">
              Explore committees, cells, and leadership teams in our alumni organization
            </p>
          </div>
          
          {/* Statistics Summary */}
          <div className="hidden md:flex items-center space-x-6 text-center">
            <div>
              <div className="text-2xl font-bold text-gray-900 dark:text-white">
                {groups.length}
              </div>
              <div className="text-sm text-gray-500 dark:text-gray-400">
                Groups
              </div>
            </div>
            <div>
              <div className="text-2xl font-bold text-blue-600">
                {groups.reduce((sum, group) => sum + group.membersCount, 0)}
              </div>
              <div className="text-sm text-gray-500 dark:text-gray-400">
                Total Members
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Filters and Search */}
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row gap-4">
          {/* Search */}
          <div className="flex-1">
            <div className="relative">
              <MagnifyingGlassIcon className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
              <input
                type="text"
                placeholder="Search groups..."
                className="w-full pl-10 pr-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-white placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
          </div>

          {/* Type Filter */}
          <div className="sm:w-48">
            <select
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              value={selectedType}
              onChange={(e) => setSelectedType(e.target.value as GroupType | 'ALL')}
            >
              {typeOptions.map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>

          {/* Show Inactive Toggle */}
          <div className="flex items-center space-x-2">
            <input
              type="checkbox"
              id="showInactive"
              className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
              checked={showInactive}
              onChange={(e) => setShowInactive(e.target.checked)}
            />
            <label 
              htmlFor="showInactive" 
              className="text-sm text-gray-700 dark:text-gray-300"
            >
              Show inactive
            </label>
          </div>
        </div>
      </div>

      {/* Groups Grid */}
      {groups.length > 0 ? (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {groups.map((group) => (
            <GroupListCard
              key={group.id}
              group={group}
              onView={handleGroupView}
              className="h-full"
            />
          ))}
        </div>
      ) : (
        <div className="text-center py-12">
          <UserGroupIcon className="w-16 h-16 text-gray-400 mx-auto mb-4" />
          <h3 className="text-xl font-medium text-gray-900 dark:text-white mb-2">
            No Groups Found
          </h3>
          <p className="text-gray-600 dark:text-gray-400">
            {searchTerm || selectedType !== 'ALL' || showInactive
              ? 'Try adjusting your search or filters'
              : 'No groups are currently available'
            }
          </p>
        </div>
      )}

      {/* Additional Info Section */}
      <div className="mt-12 bg-blue-50 dark:bg-blue-900/20 rounded-lg p-6">
        <h3 className="text-lg font-semibold text-blue-900 dark:text-blue-100 mb-3">
          About Our Groups
        </h3>
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
          <div>
            <h4 className="font-medium text-blue-800 dark:text-blue-200 mb-2">Cells</h4>
            <p className="text-sm text-blue-700 dark:text-blue-300">
              Specialized working groups focused on specific areas of activity
            </p>
          </div>
          <div>
            <h4 className="font-medium text-blue-800 dark:text-blue-200 mb-2">Committees</h4>
            <p className="text-sm text-blue-700 dark:text-blue-300">
              Advisory and decision-making bodies for organizational governance
            </p>
          </div>
          <div>
            <h4 className="font-medium text-blue-800 dark:text-blue-200 mb-2">Office Bearers</h4>
            <p className="text-sm text-blue-700 dark:text-blue-300">
              Executive leadership team managing day-to-day operations
            </p>
          </div>
          <div>
            <h4 className="font-medium text-blue-800 dark:text-blue-200 mb-2">Advisors</h4>
            <p className="text-sm text-blue-700 dark:text-blue-300">
              Experienced members providing guidance and strategic direction
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Groups